import React, { Component } from 'react';
import PropType from 'prop-types';
import _ from 'lodash';
import './conference.css';
import Switch from './switch/switch.component';

class Conference extends Component {
  constructor() {
    super();

    this.state = {
      selectedConference: 0,
      isSwitcherOpened: false,
    };

    this.switchConference = this.switchConference.bind(this);
    this.openSwitcher = this.openSwitcher.bind(this);
  }

  openSwitcher() {
    this.setState({
      isSwitcherOpened: !this.state.isSwitcherOpened,
    });
  }

  switchConference(index) {
    this.setState({
      selectedConference: index,
    });
  }

  render() {
    const { conferences, timeBegin, timeEnd } = this.props;
    const conference = conferences[this.state.selectedConference] || '';
    const remaningConferences = _.pull(...conferences, conference);

    return (
      <div>
        <div className="columns">
          <div className="column">
            <div className="conference">
              <div className="conference-opt">
                <a>
                  <i onClick={this.openSwitcher} className="fa fa-arrows-h circle" />
                </a>
                <span className="conference-time">{`${timeBegin}h00 > ${timeEnd}h00`}</span>
                <a>
                  <i className="fa fa-lock circle" />
                </a>
              </div>
              <div className="conference-title">
                {conference.name !== undefined ? conference.name : 'Temps libre'}
              </div>
            </div>
          </div>
        </div>
        {this.state.isSwitcherOpened
          ? <Switch conferences={remaningConferences} switchConference={this.switchConference} />
          : null}
      </div>
    );
  }
}
// {conference
//   ? <div>
//     <button className="lockButton">Lock</button>
//     <br />
//     <span>
//       {conference.name !== undefined ? conference.name : 'Temps libre'}
//     </span>
//   </div>
//   : 'foo'}
// <span>{`${conference.timeBegin} - ${conference.timeEnd}`}</span>

Conference.propTypes = {
  conferences: PropType.arrayOf({
    name: PropType.string.isRequired,
    timeBegin: PropType.string.isRequired,
    timeEnd: PropType.string.isRequired,
  }).isRequired,
  timeBegin: PropType.number.isRequired,
  timeEnd: PropType.number.isRequired,
};

export default Conference;



